export default {
  computed: {
  },
  data () {
    return {
      tabIdx: 0,   // 选项卡
      courseId: null,  // 系列课程id
      courseDetail: ''   // 系列课程详情
    }
  },
  mounted () {
    let that = this
    that.$nextTick(() => {
      that.getCourseDetail()
    })
    that.courseId = that.$route.query.courseId
  },
  methods: {
    payClick () {

    },
    // 选项卡切换
    checkIntroClick () {
      this.tabIdx = 0
    },
    checkCatelog () {
      this.tabIdx = 1
    },
    // 课程系列详情
    getCourseDetail () {
      let that = this
      let data = {
        id: that.courseId,  // 系列课程id
        token: localStorage.getItem('token')
      }
      that.axios({
        method: 'POST',
        url: 'interface/Course/column_detail',
        data: data
      }).then((res) => {
        console.log('课程系列详情', res)
        if (res.err_code !== 1) {
          this.showAlert(res.msg)
          return false
        }
        that.courseDetail = res.data
      })
    }
  }
}



